@import url("https://fonts.googleapis.com/css?family=Open+Sans:400,700&display=swap");
:root {
  --primary-color: rgb(17, 86, 102);
  --primary-color-darker: rgb(9, 48, 56);
  font-size: 62.5%;
}

* {
  box-sizing: border-box;
  outline: 0;
  padding: 0;
  margin: 0;
}

body {
  margin: 0;
  padding: 0;
  background: var(--primary-color);
  font-family: "Open sans", sans-serif;
  font-size: 2rem;
  line-height: 3rem;
}

h1,
h2,
h3 {
  margin: 1rem 0;
}

.container {
  max-width: 640px;
  margin: 50px auto;
  background: #fff;
  padding: 20px;
  border-radius: 10px;
}

.block {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem;
  width: 100%;
}
.block .imc {
  display: flex;
  flex-direction: column;
}
.block ul {
  width: 100%;
}
.block ul li {
  list-style: none;
  text-align: left;
}

form input,
form label,
form button {
  display: block;
  width: 90%;
  margin-bottom: 10px;
  margin: 0 auto;
}

form input {
  font-size: 24px;
  height: 50px;
  padding: 0 20px;
}

form input:focus {
  outline: 1px solid var(--primary-color);
}

form button {
  border: none;
  background: var(--primary-color);
  color: #fff;
  font-size: 18px;
  font-weight: 700;
  height: 50px;
  cursor: pointer;
  margin-top: 1rem;
}

form button:hover {
  background: var(--primary-color-darker);
}

.alert {
  height: 3rem;
  width: 90%;
  margin: 0 auto;
  margin-top: 2rem;
}
.alert .alertSpan {
  color: red;
  font-size: 1.3rem;
  position: relative;
  right: 0;
}

.result {
  min-height: 40px;
  width: 90%;
  margin: 2rem auto;
}
.result .imcblock {
  margin: 2rem 0;
}

